import React, { useState } from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CCardGroup,
  CCol,
  CContainer,
  CForm,
  CFormCheck,
  CFormInput,
  CInputGroup,
  CRow,
} from '@coreui/react';
import './Login.scss';
import { BsBoxArrowInRight } from "react-icons/bs";
import { Link, useNavigate } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { setToken, clearToken } from '../../../redux/actions';
import { ToastContainer, toast } from 'react-toastify';

const Login = () => {
  const token = useSelector((state) => state.token);
  const dispatch = useDispatch();
  const Navigate = useNavigate();

  const url = process.env.REACT_APP_API_URL+"admin/login";
  const [userInfo, setUserInfo] = useState({});
  const handleLogin = (e) => {
    e.preventDefault();
    const [username, password, keepMeLogin] = e.target;
    fetch(url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        user: username.value,
        password: password.value,
      }),
    })
      .then(async (response) => {
        return response.json();       
      })
      .then((result) => {  
        if(result.status==="200"){
          setUserInfo(result.data);
          localStorage.setItem("token", result.data.token);
          dispatch(setToken(result.data.token));
          toast.success(result.message);
        } else{
          toast.error(result.message);
        }     
        
        window.location.reload();
      })
      .catch((error) => {    
        toast.error(error.message)
        console.error('Error during login:', error);
      });
  };

  return (<>
    <ToastContainer/>
    <div className="arLoginBg min-vh-100 d-flex flex-row align-items-center login-sec">
      <CContainer>
        <CRow className="justify-content-center">
          <CCol md={8} sm={6}>
            <CCardGroup>
              <CCard className="p-4">
                <CCardBody>
                  <CForm onSubmit={handleLogin}>
                    <div className='mb-3'>
                      <img src={require("../../../assets/brand/logo.png")} height={54}/> 
                    </div>

                    <div className='each_form_group'>
                      <label htmlFor="exampleInputEmail1" className="form-label">Username</label>
                      <CInputGroup>
                        <CFormInput autoComplete="username" name='username'/>
                      </CInputGroup>
                    </div>
                    <div className='each_form_group'>
                      <label htmlFor="exampleInputEmail1" className="form-label">Password</label>
                      <CInputGroup>
                        <CFormInput type="password" autoComplete="current-password" name='password'/>
                      </CInputGroup>
                    </div>
                    <CRow className='justify-content-between'>
                      <CCol xs={"auto"} className='icons'>
                        <div className='d-flex align-items-center'>
                          <CFormCheck id='keepMeLogin' name='keepMeLogin'></CFormCheck>
                          <label htmlFor='keepMeLogin' className='para_1'>Keep me  logged  in</label> 
                        </div>
                      </CCol>
                      <CCol xs={"auto"}>
                        <Link className='para_2' to={'/forget-password'}>Forgot Password?</Link>
                      </CCol>                      
                    </CRow>
                    <div className='mt-5 text-center'>
                        <CButton color="primary" type='submit'>Login<BsBoxArrowInRight color='primary'/></CButton>
                    </div>
                  </CForm>                  
                </CCardBody>
              </CCard>              
              <CCard className="rightLoginBg text-white">
                 <img src={require("../../../assets/images/login-img.png")} height="100%" width={"auto"} alt='login image'/>
               </CCard>
            </CCardGroup>
          </CCol>
        </CRow>
      </CContainer>
    </div>
    </>
  )
}

export default Login
